.feedbacks {
  text-align: center;

  &__inner {
    margin: 15px 0 5px;

    h3 {
      @include hFont(32px, 300, $fontLato);
      margin: 70px 0 40px;
    }

    p {
      max-width: 570px;
      @include font(20px);
      color: $main-text-color;
      padding: 10px 0;
      margin: 0 auto 25px;
    }

    .author {
      margin: 60px;
      @include font(12px, 700);
      text-transform: uppercase;
      color: $black;
    }
  }

  &__photoes {
    display: flex;
    justify-content: center;
    align-items: center;

    .photoes_item {
      width: 90px;
      height: 90px;
      margin: 0 65px;
      position: relative;

      &:before {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 10;
        background-color: rgba(255, 255, 255, 0.6);
      }

      &:nth-child(2) {
        width: 130px;
        height: 130px;

        &:before {
          background-color: rgba(255, 255, 255, 0);
        }

        img {
          max-width: 1390px;
        }
      }

      img {
        max-width: 90px;
        width: 100%;
        @include border(0, 50%);
      }
    }

